/* 메인 페이지 */
.container {
    width: 700px;

    text-align: center;

    margin: 50px auto;

    line-height: 0px;
}

.container .title {
    margin-bottom: 70px;
}

.container .name {
    width: 160px;
    height: 50px;

    margin : 0 auto 30px;
    padding : 0px 0px;
}

.container .name p {
    text-align: left;

    font-size: 15px;
    font-weight: bold;
    color: #4b5c6b;

    margin-bottom: 10px;
}

.container .name input {
    all: unset;

    width: 160px;
    height: 35px;

    border-color: #d3dae0;
    border-width: 3px;
    border-style: solid;
    border-radius: 5px;
}

.container .gender {
    width: 60px;

    line-height: 25px;

    margin : 0 auto;
    padding : 0px 0px;
}

.container .gender div {
    font-weight: 500;
}

.container .gender p {
    margin : 0px;
    padding-left: 10px;

    text-align: left;

    font-size: 15px;
    font-weight: bold;
    color: #4b5c6b;
}

.container .gender input[type="radio"] {
    -webkit-appearance:none;
    -moz-appearance:none;
    -ms-appearance:none;
    -o-appearance:none;

    width: 13px;
    height: 13px;

    border: 2px solid darkgray;
    border-radius: 13px;
    background: #ffffff;
}

.container .gender input[type="radio"]:checked::before {
    content:'';
    display:block;
    width: 75%;
    height:75%;
    margin: 13% auto;
    border-radius: 100%;
    background: #6558f5;
}

.container .buttonContainer {
    margin-top: 30px;
}
